<template>
  <div>
    <el-dialog :visible.sync="visible" :before-close="beforeClose" :append-to-body="false" :modal-append-to-body="false"
               title="选择部门" width="500px">
      <div class="p15">
        <el-row>
          <el-col :span="20">
            <Tree @callback="treeCallback" :reload="reloadTree" @finishReload="finishReload" ></Tree>
          </el-col>
        </el-row>
      </div>
      <div slot="footer" class="dialog-footer mt10" style="text-align: center">
        <el-button type="primary" @click="closeDialog('save')">保 存</el-button>
        <el-button @click="closeDialog('close')">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import Tree from '../common/Tree.vue'

  export default {
    name: 'Dialog',
    components: {Tree},
    props: ['dialogFormVisible'],
    computed: {
      visible: {
        get: function () {
          return this.dialogFormVisible
        },
        set: function () {
        }
      }
    },
    data() {
      return {
        showModal: false,
        reloadTree: false,
        treeBackData: {}
      }
    },
    mounted() {
    },
    methods: {
      closeDialog(method) {
        this.$emit('closeDialog', this.treeBackData)
        if (method !== 'close') {
          this.reloadTree = true
        }
      },
      modalShow() {
        this.showModal = true
      },
      closeModal(value) {
        this.form.department = value
      },
      beforeClose(done) {
        done()
        this.$emit('closeDialog')
      },
      finishReload() {
        this.reloadTree = false
      },
      treeCallback(obj) {
        console.log('treeCallback')
        console.log(obj)
        if (obj.selectDepartment) {
          this.treeBackData = obj.selectDepartment
        }
      },
    }
  }
</script>
<style lang="scss">
  .dialog_input_custom {
    > input {
      height: 28px;
      font-size: 12px;
      top: 1px;
      position: relative;
    }
  }

  .dialog_date_custom {
    .el-input__prefix {
      top: -6px;
    }
  }
</style>


