<template>
  <div>
    <el-dialog :visible.sync="visible" :before-close="beforeClose" :modal-append-to-body="false" title="新增人员" width="800px">
      <div class="p15">
        <el-form :show-message=true :inline-message=true ref="form" :model="form" :rules="rules" class="form-text f13 " label-width="108px" size="mini">
          <el-row>
            <el-col :span="16">
              <el-form-item label="姓名：" prop="staff_name">
                <el-input
                  placeholder='请输入姓名'
                  v-model='form.staff_name'>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="16">
              <el-form-item label="工号：" prop="job_no">
                <el-input
                  placeholder='请输入工号/账号'
                  v-model='form.job_no'>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="16">
              <el-form-item label="手机号：" prop="phone">
                <el-input
                  placeholder='请输入手机号'
                  v-model='form.phone'>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="16">
              <el-form-item label="初始密码：" prop="password" ref="password">
                <el-input
                  placeholder='请输入初始密码'
                  v-model='form.password'
                  type="password" >
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="16">
              <el-form-item label="性别：">
                <el-radio-group v-model="form.sex">
                  <el-radio :label="1">男</el-radio>
                  <el-radio :label="0">女</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="16">
              <el-form-item label="所属部门：">
                <el-input
                  placeholder='请选择部门'
                  @click.native="showDepartmentSelect"
                  v-model="form.department_name"
                  readonly>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="16" class="dialog_input_custom">
              <el-form-item label="职务：">
                <el-select v-model="form.duty" placeholder="选择职务">
                  <el-option v-for="(label, index) in roleDict" :label='label' :value='index' :key="index"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="16">
              <el-form-item label="邮箱：" prop="email">
                <el-input
                  placeholder='请选择所在组织部门'
                  v-model='form.email'
                  type="email" >
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5" >
            <el-col :span="16" >
              <el-row type="flex" justify="space-between">
                <el-col :span="19">
                  <el-form-item label="办公电话：">
                    <el-input
                      placeholder='请选择所在组织部门'
                      v-model='form.office_phoneFront'
                      type="email" >
                    </el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="4">
                  <el-input
                    placeholder='分机'
                    v-model='form.office_phoneEnd'
                    class='dialog_input_custom' >
                  </el-input>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="16">
              <el-form-item label="微信号：">
                <el-input
                  placeholder='请输入微信号'
                  v-model='form.wechat_no'>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="16" class="dialog_input_custom">
              <el-form-item label="出生日期：" prop="birth_date">
                <el-date-picker id='born' class='dialog_date_custom' type="date" placeholder="请选择出生年月日" v-model="form.birth_date" value-format="timestamp"></el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="16" class="dialog_input_custom">
              <el-form-item label="入职时间：" prop="join_date">
                <el-date-picker id='date' class='dialog_date_custom' type="date" placeholder="请选择毕业时间" v-model="form.join_date" value-format="timestamp"></el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="16">
              <el-form-item label="用户状态：">
                <el-radio-group v-model="form.status">
                  <el-radio :label="1">启用</el-radio>
                  <el-radio :label="0">禁用</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <el-form-item label="备注：" ref="end">
                <el-input
                  type="textarea"
                  :rows="4"
                  v-model="form.remark"
                >
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer mt10" style="text-align: center">
        <el-button type="primary" @click="closeDialog('again')">保存并继续添加</el-button>
        <el-button type="primary" @click="closeDialog('save')">保 存</el-button>
        <el-button @click="closeDialog('close')">取 消</el-button>
      </div>
    </el-dialog>
    <DepartmentSelect @closeDialog="closeDepartmentDialog" :dialogFormVisible="departmentDialogShow"></DepartmentSelect>
  </div>
</template>
<script>
  import DepartmentSelect from './DepartmentSelect'
  import { operateEmployees } from '../constants'
  import { mapActions } from 'vuex'
  import formMixin from '@/mixins/addFormMixin'

  export default {
    name: 'Dialog',
    components: {
      DepartmentSelect
    },
    props: ['dialogFormVisible'],
    mixins: [formMixin(operateEmployees)],
    computed: {
      visible: {
        get: function () {
          return this.dialogFormVisible
        },
        set: function () {
        }
      }
    },
    data() {
      return {
        departmentDialogShow: false,
        roleDict: ['部门主管', '维护人员', '组长', '巡检人员'],
      }
    },
    methods: {
      ...mapActions(['postEmployees']),
      formHandler() {
        const newParams = {office_phone: [], role_ids: [], role_names: []}
        Object.keys(this.form).map((key) => {
          if(key === 'office_phoneFront') {
            return newParams['office_phone'][0] = this.form[key]
          }
          if(key === 'office_phoneEnd') {
            return newParams['office_phone'][1] = this.form[key]
          }
          if(key === 'duty') {
            // newParams['role_ids'].push(this.form[key])
            // newParams['role_names'].push(this.roleDict[this.form[key]])
            return
          }
          newParams[key] = this.form[key]
        })
        newParams['office_phone'] = newParams['office_phone'].join('-')
        return newParams
      },
      closeDialog(method) {
        if (this.$refs.end.error) {
          this.$refs.end.error = undefined
        }
        if (method === 'close') {
          this.$emit('closeDialog', {close: true, reload: false})
        } else {
          this.validateForm().then(() => {
            const newParams = this.formHandler()
            this.postEmployees(newParams).then(() => {
              this.initForm()
              if (method === 'save') {
                this.$emit('closeDialog', {close: true, reload: true})
              } else {
                this.$emit('closeDialog', {close: false, reload: true})
              }
            }).catch((err) => {
              return this.$refs.end.error = err
            })
          })
        }
      },
      showDepartmentSelect() {
        console.log('click')
        this.departmentDialogShow = true
      },
      closeDepartmentDialog(value) {
        if (value && value['department_id']) {
          this.form['department_id'] = value['department_id']
          this.form['department_name'] = value['department_name']
        }
        this.departmentDialogShow = false
      },
      beforeClose(done) {
        done()
        this.$emit('closeDialog')
      }
    }
  }
</script>
<style lang="scss">
  .dialog_input_custom{
    .el-input__suffix {
      top: -5px;
    }
    >input{
      height: 28px;
      font-size: 12px;
      top: 1px;
      position: relative;
    }
  }
  .dialog_date_custom{
    .el-input__prefix{
      top: -6px;
    }
  }
</style>


