<template>
  <div>
    <div class='searchInput' v-if="filter">
      <el-input
        placeholder='搜索'
        v-model='filterText'>
        <i slot='prefix' class='el-input__icon el-icon-search'></i>
      </el-input>
    </div>
    <el-tree
      :data='tree'
      :props='defaultProps'
      node-key="department_id"
      @node-click="treeClick"
      :default-expanded-keys="expandKey"
      ref="tree"
      :filter-node-method="filterNode"
      :show-checkbox="checkbox || false"
      @check-change="handleCheckChange"
    >
    </el-tree>
  </div>
</template>

<script>
  import {mapActions, mapGetters} from 'vuex'

  export default {
    name: 'Tree',
    computed: {
      tree() {
        if (this.propData) {
          return this.propTree || []
        }
        return this.departmentTree()
      }
    },
    props: ['reload', 'filter', 'propTree', 'propData', 'checkbox'],
    data() {
      return {
        defaultProps: {
          children: 'child_list',
          label: 'department_name'
        },
        selectDepartment: '',
        clickNode: {},
        expandKey: [],
        nodeLayer: 0,
        filterText: ''
      }
    },
    methods: {
      ...mapActions(['getDepartmentTree']),
      ...mapGetters(['departmentTree']),
      initData() {
        if (this.propData) {
          if (this.tree[0] && this.tree[0]['department_id']) {
            let id = this.tree[0]['department_id']
            this.$refs.tree.setCurrentKey(id, true)
            // this.expandKey[0] = this.tree[0]['department_id']  默认展开
            this.selectDepartment = this.tree[0]
            this._callback(id)
          }
        } else {
          this.getDepartmentTree().then(() => {
            if (this.tree[0] && this.tree[0]['department_id']) {
              let id = this.tree[0]['department_id']
              this.$refs.tree.setCurrentKey(id, true)
              // this.expandKey[0] = this.tree[0]['department_id']  默认展开
              this.selectDepartment = this.tree[0]
              this._callback(id)
            }
          })
        }
      },
      reloadData() {
        if (this.selectDepartment && this.selectDepartment['department_id']) {
          let id = this.selectDepartment['department_id']
          this.getDepartmentTree().then(() => {
            this.$refs.tree.setCurrentKey(id, true)
          })
          this._callback(id, this.selectDepartment)
        } else {
          this.initData()
        }
      },
      treeClick(obj, node) {
        if (this.clickNode === node && this.selectDepartment === obj.id) {
          return
        }
        this.selectDepartment = obj
        this.clickNode = node
        this.nodeLayer = node.level
        this.getExpand(node, true)
        this._callback(this.selectDepartment['department_id'], obj)
      },
      handleCheckChange() {

      },
      getExpand(node, reset) {
        if (reset) {
          this.expandKey = []
        }
        if (node.isLeaf) {
          this.getExpand(node.parent)
        } else {
          this.expandKey.push(node.key)
        }
      },
      filterNode(value, data) {
        if (!value) return true
        return data['department_name'].indexOf(value) !== -1
      },
      _callback(id, obj) {
        this.$emit('callback', {id, nodeLayer: this.nodeLayer, selectDepartment: this.selectDepartment, _data: obj})
      }
    },
    mounted() {
      this.initData()
    },
    watch: {
      reload() {
        if(this.reload.reload) {
          if (this.reload.init) {
            this.selectDepartment = {}
          }
          this.reloadData()
          this.$emit('finishReload')
        }
      },
      filterText(val) {
        this.$refs.tree.filter(val)
      },
      propTree() {
        this.initData()
      }
    }
  }
</script>


